.companions {
  margin-top: -50px;
  position: relative;
  background-color: #161c36;
  border-radius: 0 0 30px 30px;
  color: inherit;
  z-index: 4;

  @media (min-width: $tablet-width) {
    padding-bottom: 87px;
    border-radius: 0 0 60px 60px;
  }

  @media (min-width: $desktop-width) {
    margin-top: -50px;
    padding-bottom: 134px;
    border-radius: 0 0 55px 55px;
  }
}

.companions .container {
  padding-bottom: 30px;

  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: 285px 285px;
    grid-template-rows: auto auto auto;
    column-gap: 50px;
    padding: 0 75px;
  }

  @media (min-width: $desktop-width) {
    grid-template-columns: 285px 285px 285px 285px;
    grid-template-rows: auto auto;
    column-gap: 60px;
    padding: 184px 60px 0;
  }
}

.companions ul {
  list-style: none;
}

.companions__wrapper {
  position: relative;
  padding: 99px 25px 38px;
  color: #ffffff;

  @media (min-width: $tablet-width) {
    grid-column: 1 / 3;
    grid-row: 1 / 2;
    padding: 99px 0 0;
    z-index: 4;
  }

  @media (min-width: $desktop-width) {
    grid-column: 1 / 3;
    grid-row: 1 / 2;
    padding: 0 100px 0 69px;
  }

  &::before {
    content: "";
    position: absolute;
    width: 112px;
    height: 132px;
    top: 85px;
    left: -25px;
    background-color: #141b3b;
    border-radius: 0 20px 20px 0;
    z-index: -1;

    @media (min-width: $tablet-width) {
      width: 212px;
      height: 212px;
      top: 121px;
      left: 0;
    }

    @media (min-width: $desktop-width) {
      top: -63px;
      left: 0;
    }
  }

  &::after {
    content: "";
    position: absolute;
    width: 143px;
    height: 143px;
    top: 84px;
    right: 0;
    background-image: url("../img/smolov-back-desktop@1x.png");
    background-repeat: no-repeat;
    border-radius: 10px;
    z-index: -1;

    @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
      background-image: url("../img/smolov-back-desktop@2x.png");
      background-size: 143px auto;
    }

    @media (min-width: $tablet-width) {
      width: 285px;
      height: 285px;
      top: 130px;
      right: 0;
      background-image: url("../img/rogovaya-back-desktop@1x.png");
      border-radius: 20px;

      @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
        background-image: url("../img/rogovaya-back-desktop@2x.png");
        background-size: 285px auto;
      }
    }

    @media (min-width: $desktop-width) {
      top: 252px;
      left: 130px;
    }
  }
}

.companions__title {
  @media (min-width: $tablet-width) {
    padding: 55px 0 17px;
    font-size: 45px;
    line-height: 45px;
    color: #ff8d30;
  }

  @media (min-width: $desktop-width) {
    margin: -2px 0 0;
    padding: 0 0 23px;
  }
}

.companions__description {
  margin-bottom: 21px;

  @media (min-width: $tablet-width) {
    margin-bottom: 0;
    padding: 0 190px 17px 0;
    font-size: 28px;
    line-height: 34px;
  }

  @media (min-width: $desktop-width) {
    padding: 0 0 17px;
  }
}

.companions__text {
  position: relative;
  font-weight: 700;

  @media (min-width: $tablet-width) {
    padding: 0 0 37px;
    width: 290px;
    font-size: 21px;
    line-height: 25px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 0;
    padding-bottom: 3px;
  }
}

.companions__list {
  padding: 0 0 9px;

  @media (min-width: $tablet-width) {
    grid-column: 1 / 3;
    grid-row: 2 / 2;
    padding-bottom: 71px;
  }

  @media (min-width: $desktop-width) {
    grid-column: 3 / 5;
    grid-row: 1 / 3;
    padding-bottom: 0;
  }

  &::after {
    content: "";
    position: absolute;
    width: 71px;
    height: 71px;
    top: -33px;
    left: -35px;
    background-image: url("../img/rogovaya-back-mobile@1x.png");
    background-repeat: no-repeat;
    border-radius: 0 5px 5px 0;
    z-index: -1;

    @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
      background-image: url("../img/rogovaya-back-mobile@2x.png");
      background-size: 70px auto;
    }

    @media (min-width: $tablet-width) {
      width: 143px;
      height: 143px;
      top: -97px;
      left: -35px;
      background-image: url("../img/smolov-back-desktop@1x.png");
      border-radius: 10px;

      @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
        background-image: url("../img/smolov-back-desktop@2x.png");
        background-size: 143px auto;
      }
    }

    @media (min-width: $desktop-width) {
      top: 0;
      left: -204px;
    }
  }
}

.companions__button {
  position: relative;
  min-height: 62px;

  @media (min-width: $tablet-width) {
    grid-column: 1 / 3;
    grid-row: 3 / 4;
  }

  @media (min-width: $desktop-width) {
    grid-column: 1 / 3;
    grid-row: 2 / 3;
    margin-top: auto;
    margin-left: 71px;
    width: 280px;
    min-height: 80px;
    z-index: 5;
  }
}
