.button {
  position: relative;
  display: block;
  padding: 22px 23px;
  font-family: inherit;
  font-weight: 700;
  font-size: 18px;
  line-height: 18px;
  text-align: left;
  color: #1d2e5b;
  
  background-color: #ffd74b;
  border-radius: 10px;
  border: none;

  @media (min-width: $tablet-width) {
    padding: 29px 0 27px 33px;
    font-size: 24px;
    line-height: 24px;
    border-radius: 20px;
    text-transform: none;
  }

  &:hover,
  &:focus {
    background-color: #ffbd30;
    transition: 0.3s;
  }

  &:active {
    color: fade-out(#000000, 0.7);
    transition: 0.3s;
  }

  &--auth {
    padding: 10px 0;
    text-align: center;
    font-size: 17px;
    line-height: 30px;
    color: #161c35;
    border-radius: 25px;
    text-transform: uppercase;

    @media (min-width: $tablet-width) {
      font-size: 20px;
      line-height: 20px;
      padding: 16px 0 14px;
    }

    // &:hover,
    // &:focus {
    //   background-color: #ebebeb;
    // }

    // &:active {
    //   color: fade-out(#444444, 0.7);
    // }
  }

  &--country {
    text-align: center;
    padding: 12px 20px 10px 45px;
    font-size: 16px;
    letter-spacing: 1px;
    border-radius: 25px;
    text-transform: uppercase;
  }

  &--triangle {
    &::after {
      content: "";
      position: absolute;
      width: 0;
      height: 0;
      border-top: 6px solid transparent;
      border-left: 11px solid #1d2e5b;
      border-bottom: 6px solid transparent;
      top: 40%;
      right: 22px;
  
      @media (min-width: $tablet-width) {
        border-top: 6px solid transparent;
        border-left: 11px solid #1d2e5b;
        border-bottom: 6px solid transparent;
        top: 43%;
        right: 35px;
      }
    }
  }

  &--main-filter {
    margin: 0 -5px;
    text-align: center;
    padding: 13px 20px 11px;
    font-size: 17px;
    line-height: 17px;
    border-radius: 25px;
    text-transform: uppercase;
    background-color: #ffffff;

    @media (min-width: $tablet-width) and (max-width: $desktop-width - 1) {
      margin: 0;
      padding: 17px 20px 13px;
      font-size: 20px;
      line-height: 20px;
    }

    &:hover,
    &:focus {
      background-color: #ffffff;
      filter: drop-shadow(0px 3px 6px rgba(0, 0, 0, 0.14));
    }

    &:active {
      background-color: #ffbd30;
      color: fade-out(#000000, 0.7);
      transition: 0.3s;
    }
  }

  &--catalog {
    margin: 0 auto;
    width: 230px;
    text-align: center;
    padding: 13px 20px 9px;
    font-size: 17px;
    line-height: 17px;
    border-radius: 25px;
    text-transform: uppercase;
    background-color: #ffd74b;

    @media (min-width: $tablet-width) {
      width: 150px;
      margin-left: 254px;
      padding: 18px 20px 12px;
      font-size: 20px;
      line-height: 20px;
    }

    @media (min-width: $desktop-width) {
      margin-left: 339px;
    }
  }
}
