
.continent__item {
  min-height: 27px;
}

.continent__link {  
  font-weight: 700;
  text-transform: uppercase;
  color: #192144;
  opacity: 0.3;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 20px;
  }
  
  &--active {
    opacity: 1;
  }

  &:hover,
  &:focus {
    opacity: 0.6;
  }

  &:active {
    opacity: 1;
  }
}